import React from "react";
import Card from "./card";

const CardList = ({ Kittens }) => {
  const cardComponent = Kittens.map((_user, i) => {
    return (
      <Card
        key={i}
        id={Kittens[i].id}
        name={Kittens[i].name}
        email={Kittens[i].email}
      />
    );
  });
  return <div>{cardComponent}</div>;
};

export default CardList;
